.filter-btn {
  border: 1px solid #ff8400;
  padding: 7px 15px;
  background: transparent;
  margin-left: 15px;
  margin-top: 10px;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.menu__title {
  color:#ff8400;
}

.active__btn {
  background: #ff8400;
}

@media only screen and (max-width: 992px) {
  .menu__title {
    font-size: 1.4rem;
  }
  .filter-btn {
    padding: 5px 15px;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 767px) {
  .filter-btn {
    margin-bottom: 1px;
  }
}
