@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap);
/* =========== google fonts =========== */

/* ======== base style ======= */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  background: #000000;
}

section,
footer {
  padding: 70px 0px;
}

h1,
h2 {
  font-size: 2rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: #ff8400;
}

p {
  font-size: 1rem;
  color: #c4c4c4;
  line-height: 30px;
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  color: unset;
}

i {
  color: #fff;
}

@media only screen and (max-width: 767px) {
  section {
    padding: 40px 0px;
  }
}

.hii{
  display: block;
  margin-top: 100PX;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: -50PX;
  width: 50%;
}
@media only screen and (min-width: 764px)
{
    .hii{
        display: block;
        margin-top: 100PX;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -50PX;
        width: 50%;
      }
}
@media only screen and (min-width: 1000px)
{
    .hii{
        display: block;
        margin-top: 100PX;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: -70PX;
        width: 35%;
      }
}
.header {
  width: 100%;
  height: 80px;
  line-height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background: #171717;
}
.image{
  height: 100px;
}

.navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo h2 {
  margin-bottom: 0;
}

.nav__list {
  display: flex;
  align-items: center;
  margin-bottom: 0;
  list-style: none;
  grid-column-gap: 2.7rem;
  -webkit-column-gap: 2.7rem;
          column-gap: 2.7rem;
}

.nav__item a {
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.nav__item a:hover {
  color: #ff8400;
}

.mobile__menu span i {
  font-size: 1.3rem;
}

.mobile__menu {
  display: none;
}


/* ==========  make it responsive ========= */

@media only screen and (max-width: 992px) {
  .nav__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    background: #1717177e;
    display: none;
  }

  .nav__list__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 250px;
    height: 100%;
    z-index: 100;
    background: #171717;
    flex-direction: column;
    box-shadow: 5px 5px 15px -5px #ff8400;
    justify-content: center !important;
    padding: 0px 20px;
  }

  .nav__list {
    flex-direction: column;
    padding: 0;
  }

  .nav__item a {
    font-size: 0.9rem;
  }

  .header {
    line-height: 40px;
    height: 60px;
    padding: 10px 0px;
  }


  .mobile__menu {
    display: block;
  }

  .logo h2 {
    font-size: 1.5rem;
  }

  .active__menu {
    display: block;
  }
}

.btn {
  padding: 7px 25px;
  border-radius: 5px;
  background: #ff8400;
  color: #fff;
  font-size: 0.9rem;
  margin-top: 30px;
}

.image{
  height: 20;
  width: 20;
  
}

.btn:hover {
  color: #fff;
}

@media only screen and (max-width: 992px) {
  .slider__content h2 {
    font-size: 1.5rem;
  }

  .slider__content p {
    font-size: 0.8rem;
  }

  .slider__content .btn {
    padding: 5px 20px;
    font-size: 0.7rem;
  }
}

@media only screen and (max-width: 767px) {
  .slider__content {
    padding-left: 0;
  }

  .slick-prev,
  .slick-next {
    display: none !important;
  }
}

.single__product {
  background: #1a1a1a;
  border-radius: 10px;
  cursor: pointer;
  -webkit-animation: animate 1s ease-in;
          animation: animate 1s ease-in;
  height: 100%;
  width: 100%;

}

.single__product:hover {
  box-shadow: 5px 5px 10px -5px #ff8400;
}

.product__content {
  padding: 0px 20px;
  padding-bottom: 15px;
  
}
.product__img{
  margin: 10px;  
  height: 75%;
}
.image{
  margin-top: 3%;
  width: 100%;
  height: 85%;
}

.product__content h6 {
  color: #ff8400;
  text-align: center;
}

.price {
  color: #fff;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

.price span {
  color: #ff8400;
  font-size: 1.2rem;
  font-weight: 500;

}


@-webkit-keyframes animate {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}


@keyframes animate {
  from {
    -webkit-transform: scale(0);
            transform: scale(0);
  }

  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@media only screen and (max-width: 500px) {
  .product__content h6 {
    font-size: 0.8rem;
  }

  .price {
    font-size: 0.7rem;
  }

  .price span {
    font-size: 0.8rem;
  }
  .product__img{
    margin: 10px;  
    height: 65%;
  }
}
@media only screen and (max-width: 767px) {
  .product__content h6 {
    font-size: 0.8rem;
  }

  .price {
    font-size: 0.7rem;
  }

  .price span {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 1400px) {
  .product__content h6 {
    font-size: 0.9rem;
  }

  .price {
    font-size: 0.8rem;
    margin-bottom: 10px;
  }

  .price span {
    font-size: 0.9rem;
  }
}


@media only screen and (max-width: 992px) {
  .popular__menu-title {
    font-size: 1.4rem;
  }
}

.filter-btn {
  border: 1px solid #ff8400;
  padding: 7px 15px;
  background: transparent;
  margin-left: 15px;
  margin-top: 10px;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}

.menu__title {
  color:#ff8400;
}

.active__btn {
  background: #ff8400;
}

@media only screen and (max-width: 992px) {
  .menu__title {
    font-size: 1.4rem;
  }
  .filter-btn {
    padding: 5px 15px;
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 767px) {
  .filter-btn {
    margin-bottom: 1px;
  }
}

.single__testimonial h6 {
  color: #ff8400;
  font-size: 1.1rem;
}

.single__testimonial p {
  font-size: 0.8rem;
  color: #fff;
}

.single__testimonial {
  padding: 20px;
}

@media only screen and (max-width: 992px) {
  .slider__content h2 {
    font-size: 1.5rem;
    margin-bottom: 15px !important;
  }

  .single__testimonial p {
    font-size: 0.7rem;
  }
}

@media only screen and (max-width: 767px) {
  .slider__wrapper {
    flex-direction: column;
  }

  .slider__content {
    width: 100% !important;
  }

  .slider__img {
    display: none;
  }
}

.footer {
  padding-bottom: 0;
}
.footer__link-title {
  color: #fff;
}

.link__item {
  padding-left: 0;
  background: transparent;
  color: #c4c4c4;
}

.link__item a {
  color: #c4c4c4;
}

.footer__bottom {
  background: #171717;
  padding: 20px 0px;
  text-align: center;
  margin-top: 50px;
}

@media only screen and (max-width: 992px) {
  .link__item a {
    font-size: 0.8rem;
  }

  .link__item {
    font-size: 0.8rem;
  }

  .logo p {
    font-size: 0.8rem;
  }
}

.single__feature span {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #ff766b;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.single__feature span i {
  font-size: 1.5rem;
}

.choose__content h4 {
  color: #fff;
  margin-bottom: 15px;
}
.single__feature h6 {
  color: #f5b70a;
  margin-bottom: 0;
}

.single__feature p {
  font-size: 0.7rem;
}

.feature__icon-two {
