.single__product {
  background: #1a1a1a;
  border-radius: 10px;
  cursor: pointer;
  animation: animate 1s ease-in;
  height: 100%;
  width: 100%;

}

.single__product:hover {
  box-shadow: 5px 5px 10px -5px #ff8400;
}

.product__content {
  padding: 0px 20px;
  padding-bottom: 15px;
  
}
.product__img{
  margin: 10px;  
  height: 75%;
}
.image{
  margin-top: 3%;
  width: 100%;
  height: 85%;
}

.product__content h6 {
  color: #ff8400;
  text-align: center;
}

.price {
  color: #fff;
  margin-bottom: 20px;
  padding-bottom: 20px;
}

.price span {
  color: #ff8400;
  font-size: 1.2rem;
  font-weight: 500;

}


@keyframes animate {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}
@media only screen and (max-width: 500px) {
  .product__content h6 {
    font-size: 0.8rem;
  }

  .price {
    font-size: 0.7rem;
  }

  .price span {
    font-size: 0.8rem;
  }
  .product__img{
    margin: 10px;  
    height: 65%;
  }
}
@media only screen and (max-width: 767px) {
  .product__content h6 {
    font-size: 0.8rem;
  }

  .price {
    font-size: 0.7rem;
  }

  .price span {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 1400px) {
  .product__content h6 {
    font-size: 0.9rem;
  }

  .price {
    font-size: 0.8rem;
    margin-bottom: 10px;
  }

  .price span {
    font-size: 0.9rem;
  }
}

